.container{
    height:100%;
    min-height: 100vh;
    width: 100%;
    background: linear-gradient(to right, #bc82e2, #d19df4);
    display: flex;
    justify-content: center;
    align-items: center;
}
.innerBox{
    min-width: 480px;
    height: fit-content;
    width: fit-content;
    background-color: #fff;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    padding: 10px 30px 30px 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    
}
.footer{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.footer .error{
    font-weight: bold;
    font-size: 0.875rem;
    color: #ff3300;
    text-align: center;
}
.footer button{
    outline: none;
    border: none;
    background-color: #b165e3;
    color: #fff;
    border-radius: 5px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px 16px;
    width: 100%;
    transition: 100ms;
    cursor: pointer;

}
.footer button:disabled{
    background-color: gray !important;
}

.footer button:hover{
    background-color: #d19df4;
}
.footer p{
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    color: #000;
}
.footer p span a{
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    color: #bc82e2;
    letter-spacing: 1px;
    font-size: 1rem;
    text-decoration: none;
}
